import React from 'react';

const Header = ({todos}) => {
    const activeTodos = () => {
        return todos.filter((el) => !el.isDone).length
    };
    const doneTodos = () => {
        return todos.filter((el) => el.isDone ).length

    };
    return (
        <div>
            <div className="todo-header">
                <h1>Todo list</h1>
                {activeTodos()} more to do, {doneTodos()} done
            </div>
        </div>
    );
};

export default Header;