import React from 'react';

const Search = ({setStatus, status ,setsearchText}) => {
    const statusHandler = (e) => {
        setStatus(e.target.value)
    };
    const searchHandler = (e) => {
        setsearchText(e.target.value)
    };
    return (
        <div>
            <div className='search'>
                <input type="text" className="search-input" placeholder='search' onChange={searchHandler}/>
                <div className="buttons">
                    <button type='button' className={`btn btn-outline-secondary ${status === 'active' ? 'active' : ''}`} value='active' onClick={statusHandler}>Active</button>
                    <button type='button' className={`btn btn-outline-primary ${status === 'done' ? 'active' : ''}`} value='done' onClick={statusHandler}>Done</button>
                </div>
            </div>
        </div>
    );
};

export default Search;