import React from 'react';

const Todo = ({name, setTodos , todos, todoObj}) => {
    const doneHandler = ()  => {
        setTodos(todos.map((todo) => todo.id === todoObj.id ? {...todo , isDone: !todo.isDone , isActive: !todo.isActive} : todo))
    };
    const importantHandler = () => {
        setTodos(todos.map((todo) => todo.id === todoObj.id ? {...todo , isImportant: !todo.isImportant } : todo))
    };
    const deleteHandler = () => {
        setTodos(todos.filter((todo) => todo.id !== todoObj.id))
    };
    const editInputHandler = (e) => {
        setTodos(todos.map((el) => el.id === todoObj.id ? {...el , todoName: e.target.value} : el))
    };
    const editFormHandler = (e) => {
        e.preventDefault()

    };
    const btnEditHandler = () => {
        setTodos(todos.map((todo) => todo.id === todoObj.id ? {...todo , isEditing: !todo.isEditing} : todo))

    };
    return (
        <li className="todo-item list-group-item d-flex justify-content-between align-items-center">
            <span className={`todo-item__name ${todoObj.isDone ? 'done' :''} ${todoObj.isImportant ? 'important' : ''}`}>{name}</span>
            <form action="" className={`edit-form ${todoObj.isEditing ? 'active' : ''} `} onSubmit={editFormHandler}>
                <input type="text" value={todoObj.todoName} onChange={editInputHandler}/>
            </form>
            <div>
                <button type='button' className={`btn btn-outline-success ${todoObj.isEditing ? 'active' : ''} `}  onClick={btnEditHandler}>{todoObj.isEditing ? 'Save' :'Edit' }</button>
                <button type='button' className={`btn btn-outline-info ${todoObj.isDone ? 'active' : ''}`} onClick={doneHandler}>Done</button>
                <button type='button' className={`btn btn-outline-warning ${todoObj.isImportant ? 'active' : ''}`} onClick={importantHandler}>Important</button>
                <button type='button' className="btn btn-outline-danger" onClick={deleteHandler}>Delete</button>
            </div>
        </li>
    );
};

export default Todo;