import React from 'react';

const AddForm = ({todos ,setTodos, inputText, setInputText}) => {
    const addHandler = (e) => {
        e.preventDefault();
        setTodos([...todos ,{todoName: inputText, id: Math.round(Math.random()*1000),
            isActive: true, isDone: false , isImportant: false, isEditing: false }]);
        setInputText('')
    };
    const inputHandler = (e) => {
        setInputText(e.target.value)
    };
    return (
        <div>
            <form action="" className="add-form d-flex justify-content-between" onSubmit={addHandler}>
                <input type="text" placeholder='What need to do' className='add-form-input'
                       onChange={inputHandler} value={inputText} required/>
                <button type='submit' className='btn btn-outline-dark'>Add Todo</button>
            </form>
        </div>
    );
};

export default AddForm;