import React from 'react';
import Todo from "./todo";

const TodoList = ({todos, setTodos, filteredTodos, searchText}) => {
    const searchTodos = filteredTodos.filter((todo) => todo.todoName.includes(searchText));
    return (
        <div>
            <ul className="todo-list list-group">
                {filteredTodos.length === 0 ? <li className='list-group-item'>Todos should de here</li> :
                    searchTodos.length === 0 ? <li className='list-group-item'>No matches</li> : searchTodos.map((todoObj, idx) => (
                        <Todo key={idx} todoObj={todoObj} name={todoObj.todoName} todos={todos} setTodos={setTodos}/>
                    ))}
            </ul>
        </div>
    );
};

export default TodoList;