import React , {useState, useEffect}from 'react';
import Header from "./header";
import Search from "./search";
import TodoList from "./todo-list";
import AddForm from "./addForm";

const App = () => {
    const [todos , setTodos] = useState([]);
    const [inputText , setInputText]= useState('');
    const [status , setStatus] = useState('active');
    const [filteredTodos, setFilteredTodos] = useState([]);
    const [searchText, setsearchText] = useState('');
    useEffect(() => {
        if (status === 'active'){
            setFilteredTodos(todos.filter((el) => el.isActive))
        } else {
            setFilteredTodos(todos.filter((el) => el.isDone))
        }
    }, [status, todos]);
    return (
        <div>
            <div className="todo-container">
                <Header todos={todos} />
                <Search setStatus={setStatus} status={status} setsearchText={setsearchText}/>
                <TodoList todos={todos} setTodos={setTodos} filteredTodos={filteredTodos} searchText={searchText}/>
                <AddForm setTodos={setTodos} todos={todos} inputText={inputText} setInputText={setInputText}/>
            </div>
        </div>
    );
};

export default App;